const catOptions=["Immobilier","Vhicules","Hi-Teck","Nautique","Meubles","Loisirs","Autres"]
               
const sousCatOptions=(cat)=>{
switch(cat){
    case "Immobilier": return ['Maisons et villas','Appartements','Terrains et villas','Vacances','Commerciale']
    case "Vhicules":return ['Voitures','Camions','Remorques','Motos','Pices de rechange','Acessoires']
    case "Loisirs":return ['Sports','Animeaux','Vlos','Musique','Livres']
    case "Hi-Teck":return ['Ordinateurs','Smartphones','Tablettes','TV','Jeux vidos et consoles','Acessoires']
    case "Meubles":return ['Sallons','Tables et chaises','Bureaux','Lits','Elements TV','Jardins']
    case "Nautique":return ['Bateaux','Piscines','Jet Ski','Pche','Autres']

    
    
    default:return []
}

}



export default{
    catOptions,sousCatOptions
}